<template>
<div class="lookOver" >
     <div class="seekName">
          <div class="seekIcon"></div><input type="text" placeholder="搜索族谱名称" name="seekVessel" class="seekVessel" form="voteform">
          <p class="wz">搜索</p>
          </div>
    <div class="familyShow">
        <form id="voteform">
            <div class="seekConditions">
                <input type="text" name="nativePlace" id="nativePlace" readonly="readonly" placeholder="籍贯地">
                <input type="text" name="familyName" class="familyName" placeholder="姓氏">
                <label for="submit" class="submit">查谱</label>
                <input type="submit" id="submit">
            </div>
        </form>
        <div class="familyCentent">
          <p>111111111</p>
          <p>111111111</p>
          <p>111111111</p>
          <p>111111111</p>
          <p>111111111</p>
          <p>111111111</p>
          <p>111111111</p>
          <p>111111111</p>
          <p>111111111</p>
          <p>111111111</p>
          <p>111111111</p>
          <p>111111111</p>
          <p>111111111</p>
          <p>111111111</p>
          <p>111111111</p>
          <p>111111111</p>
          <p>111111111</p>
          <p>111111111</p>
          <p>111111111</p>
          <p>111111111</p>
          <p>111111111</p>
          <p>111111111</p>
          <p>111111111</p>
          <p>111111111</p>
          <p>111111111</p>
          <p>111111111</p>
          <p>111111111</p>
          <p>111111111</p>
          <p>111111111</p>
          <p>111111111</p>
          <p>111111111</p>
          <p>111111111</p>
          <p>111111111</p>
          <p>111111111</p>
          <p>111111111</p>
          <p>111111111</p>
          <p>111111111</p>
          <p>111111111</p>
          <p>111111111</p>
          <p>111111111</p>
          <p>111111111</p>
          <p>111111111</p>
          <p>111111111</p>
          <p>111111111</p>
          <p>111111111</p>
          <p>111111111</p>
        </div>
    </div>
    <div id="targetContainer"></div>
</div>
</template>
<script>
/***************************组件引入区*************************************/
import BottomNav from '../share/BottomNav';//引入底部组件
/***********************************************************************/
/***************************Js引入区*************************************/
import pullDwon from '../share/js/pullDown';//引入下拉隐藏搜索栏
import MultiPicker from 'mob-multi-picker';//引入点击显示选择栏
/***********************************************************************/

export default { 
        components: {
                BottomNav
        },
        mounted() {
                this.a(),
                this.b()
        },
        methods: {
                a(){
                            this.pullDwon({
                                     name:"seekName",
                                     familyShow:"familyShow"
                            });
        },
                b(){
                        new MultiPicker({
                                input: 'nativePlace',//点击触发插件的input框的id
                                container: 'targetContainer',//插件插入的容器id
                                jsonData: this.data.dataFmily,
                                success: function (arr) {
                                        var content = '';
                                                for(let i=0;i<arr.length;i++){
                                                        if(arr[i].value != "请选择"){
                                                                if(content === ''){
                                                                        content = content+arr[i].value;
                                                                }else{
                                                                        content = content+"-"+arr[i].value;
                                                                }
                                                                        
                                                        }
                                                }
                                                        document.getElementById("nativePlace").value = content
                                                },
                                });
                        }
                }

}
</script>
<style lang="scss">
@import './css/seek.scss';
.lookOver{
        position: relative;
        width:25%;
        float:left;
}
.topShare {
        width: 100%;
        height: 2rem;
        text-align: center;
        background: #c1ad53;
        padding: 1rem;
        > h1 {
                margin: 0;
                font-size: 1.5rem;
                color: #000;
        }
}
</style>


