<template>
<div class="bottomNav">
		<ul>
				<li @click="Jump()"><img src="./img/ta.png"><p class="ace">{{listS.index}}</p></li>
				<li @click="search()"><img src="./img/puy.png"><p>{{listS.search}}</p></li>
				<li @click="Jump()"><img src="./img/txly.png"><p>{{listS.communication}}</p></li>
				<li @click="Jump()"><img src="./img/user.png"><p>{{listS.my}}</p></li>
		</ul>
</div>
</template>

<script>
export default { 
    data() {
		    return{
				    listS:{
						    index:"首页",
						    search:"查谱",
						    communication:"通讯",
						    my:"我的",
				    }
		    }
    },
		mounted() {
    },
		methods:{
				search(){
            this.$router.push({path:'/lookOver',query:{id:1}});
        }
		}
}

</script>
<style lang="scss">
.bottomNav {
        position: fixed;
        width: 100%;
        height: 4.4rem;
        background: #fff;
        bottom: 0;
        box-sizing: border-box;
        > ul {
                width: 100%;
                margin: 0!important !important;
                list-style: none;
                padding: 0;
                height: 100%;
                box-sizing: border-box;
                > li {
                        width: 25%;
                        float: left;
                        height: 3.6666666rem;
                        text-align: center;
                        box-sizing: border-box;
                        padding:0.8333333rem 2.3333333rem;
                        text-align: center;
                        position:relative;
                        >img{
                              width:1.5rem;
                              height:2rem;
                              position:absolute;
                              top:50%;
                              left:50%;
                              transform: translate(-50%,-50%);
                        }
                        >p{
                            display: inline-block;
                            font-size:10px;
                            position:absolute;
                            left:50%;
                            transform: translateX(-50%);
                            bottom:-7px;
                        }
                        >.ace{
                            color:#c1ad54;
                        }
                }
        }
}
</style>


