<template>
<div class="topShare">
  				<h1>{{name[this.$route.query.id]}}</h1>
</div>
</template>

<script>
export default { 
data () {
		return{
				name:[
						"姓氏源",
						"查谱"
				]
		}
}
}
</script>

<style lang="scss">
.topShare {
        width: 100%;
        height: 2rem;
        text-align: center;
        background: #c1ad53;
        padding: 1rem;
        > h1 {
                margin: 0;
                font-size: 1.5rem;
                color: #000;
        }

}
</style>


