<template>
<div class="groupLanguage">
		<topShare></topShare>
		<topName></topName>
		<div class="content">
				<div class="headline"><i></i><p>派语</p><span>目录</span></div>
				</div>
		</div>
</div>
</template>
<script>
/*******************组件引用部分*******************/
import topShare from '../share/topShare'
import topName from '../share/topName'
/**************************************************/
/*******************js引用部分*******************/
/**************************************************/
export default{
		data(){
				return{
						data:''
				}
		},
		components:{
				topShare,
				topName
		},
		mounted() {
				this.uu()
		},
		methods:{
				uu(){
						var json = this.paiyu.data.paiyu.split( "。" );
						var arr = [];
						const content = document.getElementsByClassName( "content" );
						for( let i = 0; i<json.length-1; i++ ){
								arr.push( json[i].split( "，" ) );
						}
						if( arr.length !=null ){
								const para=document.createElement( "div" );
								para.className  = "groupNumber";
								var number = Math.ceil( arr.length/4 );
								for( let i = 0; i<number; i++ ){
										content[0].appendChild(para.cloneNode(true));
								}
								const groupNumber = document.getElementsByClassName("groupNumber");
								const ht=document.createElement( "div" );
								const left=document.createElement( "div" );
								const right=document.createElement( "div" );
								ht.className = "ht";
								left.className = "left";
								right.className = "rigth";
								for(let i = 0; i<groupNumber.length; i++){
										for(let j = 0; j<4; j++){
												groupNumber[i].appendChild(ht.cloneNode(true));
										}
								}		
								const htNumber = document.getElementsByClassName("ht");
								for(let i = 0;i<htNumber.length; i++){
										htNumber[i].appendChild(left.cloneNode(true));
										htNumber[i].appendChild(right.cloneNode(true));
								for(let j = 0; j<htNumber[0].getElementsByTagName("div").length; j++){
												htNumber[i].getElementsByTagName("div")[j].innerHTML = arr[i][j];
								}
								}
						}
				}
		}

}
</script>

<style lang="scss">
@import './css/paiyu'
</style>


