<template>
<div class="familyName" >
		<!-- 顶部共用部分 -->
		<topShare></topShare>
		<div class="nameImg">
		</div>
		<!-- 内容区域 -->
		<div class="content" >
				<div class="masking">
						<p>点击关闭</p>
				</div>
		</div>
		<!-- 左边的导航条地区 -->
		<div class="rightNav">
				<ul>
						<li>词条目录</li>
						<li>词条目录</li>
						<li>词条目录</li>
						<li>词条目录</li>
						<li>词条目录</li>
						<li>词条目录</li>
				</ul>
		</div>
</div>
</template>
<script>
/*******************组件引用部分*******************/
import topShare from '../share/topShare'
/**************************************************/

/*******************js文件地区*************************/
import rNav from '../share/js/rNav'
/*****************************************************/
export default {
		data () {
				return{
						data:''
				}
		},
		components:{
				topShare
		},
		mounted() {
				this.rightFlex()
    	},
    	methods: {
    			rightFlex(){
    					this.rightNav({
								touchTier:'content',//触摸层的类名
								rightNav:'rightNav',//右侧导航的类名
								masking:'masking'//蒙版层类名
						});
    				}
    	}
}
</script>

<style lang="scss">
@import './css/familyName.scss';
</style>


