exports.install = function (Vue, options) {
		Vue.prototype.slid=(conf)=>{
				const overAll = document.getElementsByClassName( conf.overAll );
				const bottomNav = document.getElementsByClassName( conf.bottomNav );
				const list = bottomNav[0].getElementsByTagName("ul")[0].getElementsByTagName("li");
				var xS;     //手指接触的时候的纵坐标;
				var xE;    //手指离开的时候的纵坐标;
				var yS;   //手指横的时候的横坐标;
				var yM;  //手指在移动时的纵坐标;
				var xM; //手指在移动时的横坐标;
				var oL;//手指接触时overAll的偏移位置
				var bW = document.body.clientWidth;
				var boolean = true;
				var qq = -(document.body.clientWidth*3);
				var ldw;
				var n;
				overAll[0].addEventListener("touchstart",(ev)=>{
						ldw = overAll[0].offsetLeft;
						xS = ev.changedTouches[0].pageX;
						yS = ev.changedTouches[0].pageY;
						oL = overAll[0].offsetLeft;
				}),
				overAll[0].addEventListener("touchmove",(ev)=>{
						xM = ev.changedTouches[0].pageX;
						yM = ev.changedTouches[0].pageY;
						pp = overAll[0].offsetLeft;
						if( yS-yM>-10 && yS-yM<10 ){
								overAll[0].style.left = -(xS-xM)+oL+"px";
						}
				})
				overAll[0].addEventListener("touchend",(ev)=>{
						if(ldw<overAll[0].offsetLeft && xS-xM<0){
								n = Math.floor(-overAll[0].offsetLeft/bW+0.1);
								overAll[0].style.left = -n*bW+"px";
						}
						if(ldw>overAll[0].offsetLeft && xS-xM>0){
								n = Math.ceil(-overAll[0].offsetLeft/bW-0.1);
								overAll[0].style.left = -n*bW+"px";
						}
						if(overAll[0].offsetLeft>=0 && xS-xM<0){
								overAll[0].style.left = "";
						}
						if(overAll[0].offsetLeft<=qq && xS-xM>0){
								overAll[0].style.left = qq+"px";
						}
						list[n].getElementsByTagName("p")[0].setAttribute("class","ace");
				})
		}
}


