// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue';
import Vuex from 'vuex';
import App from './App';
import router from './router';
import VueAwesomeSwiper from 'vue-awesome-swiper';
import axios from 'axios';
import vuex from 'vuex';

//全局js引入区
import rNav from './components/share/js/rNav';//右侧导航条的伸缩效果
import pullDwon from './components/share/js/pullDown';//右侧导航条的伸缩效果
import slid from './components/share/js/slid';
//数据的引入
import data from './data/city';
import paiyu from './data/groupLanguage';
Vue.prototype.data = data;
Vue.prototype.paiyu = paiyu;
Vue.use(rNav)//绑定全局
Vue.use(pullDwon)//绑定全局
Vue.use(slid)
Vue.use(Vuex)


Vue.config.productionTip = false
require('swiper/dist/css/swiper.css');
/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  template: '<App/>',
  components: { App }
})



