exports.install = function (Vue, options) {
		Vue.prototype.pullDwon=function(conf){
				const touchPoint = document.getElementsByClassName(conf.name);//获取滑动位置
				const familyShow = document.getElementsByClassName(conf.familyShow);
				var yS;//获取touchstart时手指位置
				var yM;//获取touchmove时手指位置
				familyShow[0].addEventListener("touchstart", (ev)=>{
					yS = ev.changedTouches[0].pageY;
				})
				familyShow[0].addEventListener("touchmove",(ev)=>{
						yM = ev.changedTouches[0].pageY;
						if(familyShow[0].scrollTop >= touchPoint[0].clientHeight){
							touchPoint[0].style.top = -touchPoint[0].clientHeight+"px";
						}
						if(yS-yM<0){
							touchPoint[0].style.top = 0;
						}
				})
		}
}


