<template>
<div class="main">
<div class="overAll">
<index></index>
<lookOver></lookOver>
<lol></lol>
<my></my>
</div>
<BottomNav></BottomNav>
</div>
</template>

<script>
/*******************组件引用部分*******************/
import index from '../index/index'
import lookOver from '../lookOver/lookOver'
import lol from '../a/a'
import my from '../my/my'
import BottomNav from '../share/BottomNav'
/**************************************************/
export default { 
		components: {
    			index,
    			lookOver,
    			lol,
    			my,
    			BottomNav
		},
		mounted() {
				this.oo();
		},
		methods:{
			oo(){
					this.slid({
							overAll:"overAll",
							bottomNav:"bottomNav"
					})
			}
		}
}
</script>

<style>
.overAll{
	width:400%;
	position:absolute;
	overflow-y:hidden;
	height:100%;
	transition: all 0.5s;
}
</style>


